import React, { useEffect, useState } from "react"
import BaseTwo from "../Components/Base/BaseTwo"
import BusCard from "../Components/Bus/BusCard"
import { getBuses } from '../Helpers/index'
import BackButton from "../Components/BackButton/BackButton"


const Buses = () => {

    const [data, setData] = useState(null)
    const [error, setError] = useState(false)
    const [loading, setLoading] = useState(true)
    useEffect(() => {
        getBuses()
            .then((data) => {
                setData(data.dbRes)
                setLoading(false)
            })
            .catch((err) => {
                setError(true)
            })
    }, [])
    return(
        <BaseTwo header={<BackButton title="Buses" />} style="bg-white rounded-t-2xl">
            <div className="flex flex-col gap-5 w-screen items-center mt-5 mb-5">
                {loading ? <h1>Loading...</h1> : <BusCard data={data} />}
            </div>
        </BaseTwo>
    )
}

export default Buses